import React, { Component } from "react";
import "./App.css";
import Digit from "./Digit/Digit";
import Button from "./Button/Button";
import Lap from "./Lap/Lap";

class App extends Component {
  constructor() {
    super();
    this.intervalValue = null;
  }

  state = {
    min: 0,
    sec: 0,
    mil: 0,
    start: false,
    laps: [
      // { min: 0, sec: 0, mil: 0 },
      // { min: 0, sec: 0, mil: 0 },
      // { min: 0, sec: 0, mil: 0 },
      // { min: 0, sec: 0, mil: 0 },
    ],
  };

  // Start Watch
  startWatch = () => {
    // change start state to pause
    this.setState({ start: true });

    this.intervalValue = setInterval(() => {
      if (this.state.mil < 9) {
        // star milliseconds
        this.setState((prev) => ({ mil: prev.mil + 1 }));
      } else {
        // reset millisecond
        this.setState({ mil: 0 });

        if (this.state.sec < 59) {
          // start second
          this.setState((prev) => ({ sec: prev.sec + 1 }));
        } else {
          // reset seconds
          this.setState({ sec: 0 });
          // start second
          this.setState((prev) => ({ min: prev.min + 1 }));
        }
      }
    }, 100);
  };

  // Pause Watch
  pauseWatch = () => {
    clearInterval(this.intervalValue);

    this.setState({ start: false });
  };

  // Delete lap Item
  deleteLap = (index) => {
    // clone array
    let laps = [...this.state.laps];
    // delete item
    laps.splice(index, 1);

    //update state
    this.setState({ laps });

    //set to local Storage//
    localStorage.setItem("laps", JSON.stringify(laps));
  };

  // Create Lap
  lapCreate = () => {
    // destructuring
    let { min, sec, mil } = this.state;

    if (min || sec || mil) {
      // create lap object
      let lap = {
        min,
        sec,
        mil,
      };
      // clone laps
      let laps = [...this.state.laps];
      laps.unshift(lap);

      // update sate
      this.setState({ laps });

      //set to local Storage//
      localStorage.setItem("laps", JSON.stringify(laps));
    }
  };

  // Reset full Watch
  resetWatch = () => {
    // stop watch
    this.pauseWatch();

    // clear timer values
    this.setState({ min: 0, sec: 0, mil: 0 });

    // clear laps values
    this.setState({ laps: [] });

    // clean local storage laps
    localStorage.removeItem("laps");
  };

  // Reset time
  resetTime = () => {
    this.setState({ min: 0, sec: 0, mil: 0 });
  };

  // get local storage data
  componentDidMount() {
    if (localStorage.getItem("laps") !== null) {
      let laps = JSON.parse(localStorage.getItem("laps"));
      this.setState({ laps });
    }
  }

  render() {
    const { min, sec, mil } = this.state;
    return (
      <div className="container">
        <div className="row">
          <div className="offset-md-2 col-md-8">
            <div className="wrapper ">
              <h2 className="text-center display-3">React Stopwatch</h2>

              <div className="control my-3 text-center">
                <Button
                  func={this.state.start ? this.pauseWatch : this.startWatch}
                  styleClass={
                    this.state.start
                      ? "btn btn-info mr-2"
                      : "btn btn-primary mr-2"
                  }
                  text={this.state.start ? "Pause" : "Start"}
                />

                <Button
                  func={this.lapCreate}
                  styleClass="btn btn-warning mr-2"
                  text="Lap"
                />

                <Button
                  func={this.resetWatch}
                  styleClass="btn btn-danger"
                  text="Reset"
                />
              </div>

              <div className="wrap-watch">
                <div className="reset-time-btn">
                  {min || sec || mil ? (
                    <button onClick={this.resetTime} className="text-danger">
                      <svg
                        width="1em"
                        height="1em"
                        viewBox="0 0 16 16"
                        class="bi bi-arrow-clockwise"
                        fill="currentColor"
                        xmlns="http://www.w3.org/2000/svg"
                      >
                        <path
                          fill-rule="evenodd"
                          d="M3.17 6.706a5 5 0 0 1 7.103-3.16.5.5 0 1 0 .454-.892A6 6 0 1 0 13.455 5.5a.5.5 0 0 0-.91.417 5 5 0 1 1-9.375.789z"
                        />
                        <path
                          fill-rule="evenodd"
                          d="M8.147.146a.5.5 0 0 1 .707 0l2.5 2.5a.5.5 0 0 1 0 .708l-2.5 2.5a.5.5 0 1 1-.707-.708L10.293 3 8.147.854a.5.5 0 0 1 0-.708z"
                        />
                      </svg>
                    </button>
                  ) : null}
                </div>
                <div className="stopwatch-board mx-auto d-flex">
                  <Digit num={min} caption="min" />
                  <Digit num={sec} caption="sec" />
                  <Digit num={mil} caption="mil" />
                </div>
              </div>

              <div className="list-wrap list-group">
                {this.state.laps.map((lap, index) => (
                  <Lap
                    key={index}
                    lap={lap}
                    id={index}
                    deleteLap={this.deleteLap}
                  />
                ))}
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default App;
